<template>
	<view>
		<view class="goods-list">
			<view v-for="(goods, i) in goodsList" :key="i" @click="gotoDetail(goods)">
				<my-goods :goods="goods"></my-goods>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// �����������
				queryObj: {
					query: '',
					cid: '',
					pagenum: 1,
					pagesize: 10
				},
				goodsList: [],
				total: 0,
				//������
				isLoading: false
			};
		},
		onLoad(options) {
			this.queryObj.query = options.query || ''
			this.queryObj.cid = options.cid || ''

			this.getGoodsList()
		},
		methods: {
			// ��ȡ��Ʒ�б����ݵķ���
			async getGoodsList(cb) {
				// �򿪽�����
				this.isLoading = true
				console.log(this.queryObj.cid);
				const res = await this.$myRequest({
					url: '/goods/catThress/' + this.queryObj.cid,
				})
				// �رս�����
				this.isLoading = false
				cb && cb()
				if (res.statusCode !== 200) return uni.$showMsg()
				this.goodsList = [...this.goodsList, ...res.data.data.list]
				this.total = res.data.data.total
			},
			gotoDetail(goods) {
				uni.navigateTo({
					url: '/subpkg/goods_detail/goods_detail?goods_id=' + goods.goodsId
				})
			}
		},
		onReachBottom() {
			if (this.queryObj.pagenum * this.queryObj.pagesize >= this.total) return uni.$showMsg(
				'Data loading completed!')

			if (this.isLoading) return

			// ��ҳ��ֵ����+1
			this.queryObj.pagenum++
			this.getGoodsList()
		},
		onPullDownRefresh() {
			// ���ùؼ�����
			this.queryObj.pagenum = 1
			this.total = 0
			this.isLoading = false
			this.goodsList = []

			//���·�����������
			this.getGoodsList(() => uni.stopPullDownRefresh())
		}
	}
</script>

